//Vue
import Vue from 'vue'
//Vuex
import Vuex from 'vuex'
//Vuex
Vue.use(Vuex)

//actions
const actions = {
    //
	patitle(context,value){
		// console.log('actionsjia',context,value)
		context.commit('PTITLE',value)
	},
	pacontent(context,value){
		context.commit('PACONTENT',value)
	},
	codeselect(context,value){
		context.commit('CODESELECT',value)
	},
}
//mutationsstate
const mutations = {
    //
	PTITLE(state,value){
		// console.log('mutationsJIA',state,value)
		state.patitle = value
	},
	PACONTENT(state,value){
		state.pacontent = value
	},
	CODESELECT(state,value){
		state.codeselectby = value
	},
}
//state
const state = {
    patitle: 'null',
	pacontent: 'null',
	codeselectby: '',
}

//store
export default new Vuex.Store({
	actions,
	mutations,
	state
})