<template>
    <div>
        <el-divider></el-divider>
        <el-table tooltip-effect="dark" max-height="575"
            :data="noticeList1.slice((currentPage - 1) * pageSize, currentPage * pageSize)"
            :default-sort="{ prop: 'createTime', order: 'descending' }">
            <el-table-column type="index" label="" width="55" align="center"></el-table-column>
            <el-table-column prop="id" label="ID" width="95" align="center">
            </el-table-column>
            <el-table-column prop="noticeTitle" label="" align="left" :show-overflow-tooltip="true">
            </el-table-column>
            <el-table-column prop="noticeContent" label="" align="left" :show-overflow-tooltip="true">
            </el-table-column>
            <el-table-column prop="createTime" label="" width="180" align="left">
            </el-table-column>
            <el-table-column label="" width="230" align="left">
                <template slot-scope="scope">
                    <el-button size="mini" type="info" icon="el-icon-view" @click="preview(scope.row)"></el-button>
                    <el-button size="mini" type="danger" @click="handleRevokeNotice(scope.row)" round></el-button>
                    <el-button size="mini" type="danger" @click="handleDeleteNotice(scope.row)" icon="el-icon-delete"
                        circle></el-button>
                </template>
            </el-table-column>
        </el-table>
        <el-pagination background layout="prev, pager, next,jumper, ->, total, slot" :total="noticeList1.length"
            @current-change="handleCurrentChange" :current-page="currentPage" :page-size="pageSize"
            style="text-align: right">
        </el-pagination>
    </div>
</template>
  
<script>
import request from '../api/request'
export default ({
    name: 'CodeSuserNoticed',
    components: {
    },
    data() {
        return {
            noticeList1: [],
            currentPage: 1, // 
            pageSize: 10, // 10
        };
    },
    //VUE
    created() {
        this.getAllNotices1();
    },
    methods: {
        //
        handleCurrentChange(currentPage) {
            this.currentPage = currentPage; //
            // console.log(this.currentPage);
        },
        //
        resetForm() {
            this.noticeList1 = [];
        },
        //
        getAllNotices1() {
            //ajax
            return request({
                method: 'get',
                url: "/susers/notices/selectBySusername", params: { susername: this.$cookies.getCookie("susername"), status: "1" }
            }).then((res) => {
                this.resetForm();
                this.noticeList1 = res.data.data;
            });
        },
        handleRevokeNotice(row) {
            this.$confirm('ID' + row.id + ', ?', '', {
                confirmButtonText: '',
                cancelButtonText: '',
                type: 'warning'
            }).then(() => {
                return request({
                    method: 'get',
                    url: "/susers/notices/handleNotice",
                    params: { susername: this.$cookies.getCookie("susername"), id: row.id, status: "0" }
                }).then((res) => {
                    if (res.data.code == 60301) {
                        this.$message.success(res.data.msg);
                        this.getAllNotices1();
                    } else {
                        this.$message.error(res.data.msg);
                    }
                });
            }).catch(() => {
                this.$message({
                    type: 'info',
                    message: ''
                });
            });
        },
        handleDeleteNotice(row) {
            this.$confirm('ID' + row.id + ', ?', '', {
                confirmButtonText: '',
                cancelButtonText: '',
                type: 'warning'
            }).then(() => {
                return request({
                    method: 'delete',
                    url: "/susers/notices/deleteNoticeById",
                    params: { susername: this.$cookies.getCookie("susername"), id: row.id }
                }).then((res) => {
                    if (res.data.code == 60311) {
                        this.$message.success(res.data.msg);
                        this.getAllNotices1();
                    } else {
                        this.$message.error(res.data.msg);
                    }
                });
            }).catch(() => {
                this.$message({
                    type: 'info',
                    message: ''
                });
            });
        },
        preview(row) {
            this.$alert(row.noticeContent,row.noticeTitle,  {
                dangerouslyUseHTMLString: true,
                confirmButtonText: '',
                // lockScroll: false,
                closeOnClickModal: true
            });
        },
    }
})

</script>
  
<style></style>
  