<template>
    <div>
        <el-divider></el-divider>
        <el-form :model="noticeForm" :rules="rules" ref="ruleForm" label-width="100px" class="demo-ruleForm">
            <el-form-item label="" prop="noticeTitle">
                <el-input v-model="noticeForm.noticeTitle" placeholder="" style="width: 1070px;"></el-input>
            </el-form-item>
            <el-form-item label="" prop="noticeContent">
                <el-input type="textarea" placeholder="" style="width: 1070px;" :autosize="{ minRows: 22 }"
                    v-model="noticeForm.noticeContent"></el-input>
            </el-form-item>
            <el-form-item>
                <el-button type="primary" @click="saveNotice()"></el-button>

                <el-button type="danger" style="float: right; margin-right: 100px;" @click="publishNotice()"></el-button>
            </el-form-item>
        </el-form>
    </div>
</template>
  
<script>
import request from '../api/request'
export default ({
    name: 'CodeSuserCreateNotice',
    components: {
    },
    data() {
        return {
            noticeForm: {},
            nid: this.$route.params.nid,
            rules: {
                // name: [
                //   { required: false, message: '', trigger: 'blur' },
                //   { min: 3, max: 5, message: ' 3  5 ', trigger: 'blur' }
                // ],

                // date: [
                //   { type: 'date', required: false, message: '', trigger: 'change' }
                // ],

                // type: [
                //   { type: 'array', required: false, message: '', trigger: 'change' }
                // ],
                // sex: [
                //   { required: false, message: '', trigger: 'change' }
                // ],
                // brief: [
                //   { required: false, message: '', trigger: 'blur' }
                // ]
            }
        };
    },
    mounted() {
        if(this.nid!=null){
            this.getNoticeById();
        }
    },
    methods: {
        //
        resetForm() {
            this.noticeForm = {};
        },
        getNoticeById() {
            //ajax
            return request({
                method: 'get',
                url:"/susers/notices/selectNoticeById",
                params: { susername: this.$cookies.getCookie("susername"), id: this.nid } }).then((res) => {
                        if (res.data.code == 60291) {
                            this.noticeForm = res.data.data;
                        } else {
                            this.$message.error(res.data.msg);
                        }   
                    });
        },
        //
        saveNotice() {
            //ajax
            this.noticeForm.susername = this.$cookies.getCookie("susername");
            this.noticeForm.status = '0'
            console.log(this.noticeForm);
            if(this.nid!=null){
                return request({
                method: 'post',
                url:"/susers/notices/updateNoticeById", data:this.noticeForm}).then((res) => {
                    if(res.data.code == 60321){
                        this.$message.success(res.data.msg);
                        this.resetForm();
                        // 1
                        setTimeout(() => {
                        this.$router.push({ name: "unnoticed" });
                    }, 1000);
                    } else if (res.data.code == 60320) {
                        this.$message.error(res.data.msg);
                    } else {
                        this.$message.error(res.data.msg);
                    }
                });
            } else {
            //ajax
            return request({
                method: 'post',
                url:"/susers/notices/save", data:this.noticeForm}).then((res) => {
                if (res.data.code == 60281) {
                    this.$message.success("");
                    this.resetForm();
                    // 1
                    setTimeout(() => {
                        this.$router.push({ name: "unnoticed" });
                    }, 1000);
                } else if (res.data.code == 60280) {
                    this.$message.error("");
                } else {
                    this.$message.error(res.data.msg);
                }
            });
        }
        },
        //
        publishNotice() {
            this.noticeForm.susername = this.$cookies.getCookie("susername");
            this.noticeForm.status = '1'
            console.log(this.noticeForm);
            if(this.nid!=null){
                return request({
                method: 'post',
                url:"/susers/notices/updateNoticeById", data:this.noticeForm}).then((res) => {
                    if(res.data.code == 60321){
                        this.$message.success(res.data.msg);
                        this.resetForm();
                        // 1
                        setTimeout(() => {
                        this.$router.push({ name: "noticed" });
                    }, 1000);
                    } else if (res.data.code == 60320) {
                        this.$message.error(res.data.msg);
                    } else {
                        this.$message.error(res.data.msg);
                    }
                });
            } else {
            //ajax
            return request({
                method: 'post',
                url:"/susers/notices/save", data:this.noticeForm}).then((res) => {
                if (res.data.code == 60281) {
                    this.$message.success("");
                    this.resetForm();
                    // 1
                    setTimeout(() => {
                        this.$router.push({ name: "noticed" });
                    }, 1000);
                } else if (res.data.code == 60280) {
                    this.$message.error("");
                } else {
                    this.$message.error(res.data.msg);
                }
            });
        }
        }
    }
})

</script>
  
<style></style>
  