<template>
  <div>
    <el-divider></el-divider>
    <el-form :model="ruleForm" :rules="rules" ref="ruleForm" label-width="100px" class="demo-ruleForm">
      <table width="92%">
        <tr>
          <td>
            <el-form-item label="" prop="name">
              <el-input v-model="ruleForm.name" style="width: 220px;"></el-input>
            </el-form-item>
            <el-form-item label="" prop="sex">
              <el-radio-group v-model="ruleForm.sex">
                <el-radio label=""></el-radio>
                <el-radio label=""></el-radio>
              </el-radio-group>
            </el-form-item>

            <el-form-item label="" prop="userdate">
              <el-date-picker type="date" placeholder="" v-model="ruleForm.userdate"
                style="width: 220px;"></el-date-picker>
            </el-form-item>
          </td>
          <td align="right">
            <table>
              <tr>
                <td align="center">
                  <CodeUserImgUpload/>
                </td>
              </tr>
              <!-- <tr>
                <td class="headerimg">
                  
                </td>
              </tr> -->
            </table>
            
          </td>
        </tr>
      </table>
      <el-form-item label="">
        <el-select v-model="ruleForm.tag" placeholder="">
          <el-option label="" value=""></el-option>
          <el-option label="Java" value="Java"></el-option>
          <el-option label="python" value="python"></el-option>
          <el-option label="C/C++" value="C/C++"></el-option>
          <el-option label="Go" value="Go"></el-option>
        </el-select>
      </el-form-item>
      <el-form-item label="" prop="brief">
        <el-input type="textarea" v-model="ruleForm.brief" style="width: 92%;" :autosize="{ minRows: 5 }"></el-input>
      </el-form-item>

      <el-form-item>
        <el-button type="primary" @click="setUserInfo()"></el-button>
      </el-form-item>
    </el-form>
  </div>
</template>
  
<script>
import request from '../api/request'
import CodeUserImgUpload from './CodeUserImgUpload.vue'
export default ({
  name: 'CodeUserInfo',
  components: {
    CodeUserImgUpload,
  },
  data() {
    return {
      ruleForm: {
        name: '',
        userdate: '',
        tag: '',
        sex: '',
        brief: '',

        dialogImageUrl: '',
        dialogVisible: false,
        disabled: false,
      },
      rules: {
        // name: [
        //   { required: false, message: '', trigger: 'blur' },
        //   { min: 3, max: 5, message: ' 3  5 ', trigger: 'blur' }
        // ],

        // date: [
        //   { type: 'date', required: false, message: '', trigger: 'change' }
        // ],

        // type: [
        //   { type: 'array', required: false, message: '', trigger: 'change' }
        // ],
        // sex: [
        //   { required: false, message: '', trigger: 'change' }
        // ],
        // brief: [
        //   { required: false, message: '', trigger: 'blur' }
        // ]
      }
    };
  },
  //VUE
  created() {
    this.getUserInfo();
  },
  methods: {
    //
    getUserInfo() {
      //ajax
      return request({
        method: 'get',
        url: "/users/getInfo", params: { username: this.$cookies.getCookie("username") }
      }).then((res) => {
        this.ruleForm = res.data.data;
        console.log(res.data);
      });
    },
    //
    setUserInfo() {
      this.ruleForm.username = this.$cookies.getCookie("username");
      console.log(this.ruleForm);
      //ajax
      return request({
        method: 'post',
        url: "/users/setInfo", data: this.ruleForm
      }).then((res) => {
        if (res.data.code == 60111) {
          this.$message.success(res.data.msg);
        } else if (res.data.code == 60110) {
          this.$message.error(res.data.msg);
        } else {
          this.$message.error(res.data.msg);
        }
      });
    },

    handleRemove(file) {
        console.log(file);
      },
      handlePictureCardPreview(file) {
        this.dialogImageUrl = file.url;
        this.dialogVisible = true;
      },
      handleDownload(file) {
        console.log(file);
      },
  }
})

</script>
  
<style>
.headerimg{
  text-align: center;
  font-size: 12px;
  color: #606266;
}
</style>
  